___CSS_LOADER_EXPORT___.push([module.id, `.nosotrosDesk {
  width: 100%;
  height: auto;
  display: none;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow-y: scroll;
}
.nosotrosDesk .conten-cont {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 3em 0em;
  gap: 2em;
}
.nosotrosDesk .conten-cont .taitl-cont {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nosotrosDesk .conten-cont .taitl-cont .taitl {
  color: #00a3c7;
  font-size: 2em;
  font-weight: 100;
}
.nosotrosDesk .conten-cont .filo {
  color: #00a3c7;
  font-weight: 100;
  font-size: 1.5em;
}
.nosotrosDesk .conten-cont .filo img {
  width: 3.8em;
  height: auto;
  filter: drop-shadow(2px 2px 0px #00a3c7);
}
.nosotrosDesk .conten-cont .separt {
  width: 50em;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 0.6em;
}
.nosotrosDesk .conten-cont .separt .subts {
  color: #00a3c7;
  font-weight: 100;
  font-size: 1.2em;
}
.nosotrosDesk .conten-cont .separt .texto {
  font-size: 0.9em;
  color: #000;
  font-weight: 100;
}
.nosotrosDesk .conten-cont .separt ul li {
  color: #000;
  font-weight: 100;
  font-size: 0.9em;
}

@media (min-width: 992px) {
  ion-header {
    display: none;
  }
  ion-content {
    display: none;
  }
  .nosotrosDesk {
    display: flex;
  }
}
.nosotrosDesk::-webkit-scrollbar {
  width: 0.35em;
  background-color: transparent;
  height: 0.7em;
}

.nosotrosDesk::-webkit-scrollbar-thumb {
  background-color: rgba(0, 162, 199, 0.4745098039);
  width: 0.25em;
